.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 20%;
}
.recipe a{
    color: #191970;
    font-family: GTHaptik,Helvetica,Arial,Sans-serif;
    font-weight: 800;
}


.recipe ol,h2{
    font-family: GTHaptik,Helvetica,Arial,Sans-serif;
    font-weight: 600;
    color: #000;
}

.recipe p{
    font-family: Verdana,Geneva, sans-serif;
    color: rgb(226, 168, 21);
    font-size: 20px;
}

.image{
    border-radius: 50%;
    width: 220px;
    height: 220px;
}