.App {
  min-height: 100vh;
  background-image: linear-gradient(to top,#e9c4bc 0%,  #c0dfe5 100%);
}

.input-icons button{
  position: absolute;
  color: white;
  font-size: 20px;
  background: dodgerblue;
  width: 50px;
  height: 38px;
  border: none;
}
#searchBar{
  text-align: center;
  width: 400px
}
#basic-nav-dropdown a:hover {
  color: #ff00ff;
}
#order a{
  color: burlywood;
  font-size: 25px;
  font-family: Arial Narrow, sans-serif;
}
.header{
  font-family: Arial Narrow, sans-serif;
  text-align: center;
  font-weight: bold;
  font-size: 50px;
  color: #c88937
}

#order a:hover{
  color: #ff00ff;
}

.contain{
  position: relative;
  width: 50%;
}
.centered{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.centered h4{
  color: white;
}
.meal-images{
  height: 500px;
  width: 470px;
}
.meal-text{
  text-align: center;
  margin-top: 25%;
}
.meal-text h1{
  font-size: 50px;
  font-family: GTHaptik,Helvetica,Arial,Sans-serif;
  font-weight: 800;
  color: #000;
}
.meal-text p{
  font-size: 20px;
  font-family: Verdana,Geneva, sans-serif;
  color: whitesmoke;
}

.overlay{
  position: absolute;
  top: 0;
  bottom: 0;
  left:0;
  right: 0;
  height: 220px;
  width: 220px;
  border-radius: 50%;
  opacity: 0;
  transition: .5s ease;
  background-color: rgba(0,0,0,0.6);
}
.overlay-meal{
  position: absolute;
  top: 0;
  bottom: 0;
  left:0;
  right: 0;
  height: 500px;
  width: 470px;
  opacity: 0;
  transition: .5s ease;
  background-color: rgba(0,0,0,0.6);
}
.overlay-meal .text a{
  color: cornsilk;
}
.contain:hover .overlay{
  opacity: 1;
}
.contain:hover .overlay-meal{
  opacity: 1;
}
.text{
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.footer{
  position: static;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #000;
  color: #ff00ff;
  text-align: center;
  width: 100%;
}

.footer h3{
  color: #ffffff;
  font: normal 36px 'Cookie', cursive;
}

.footer h3 span{
  color: #e0ac1c;
}

.footer p{
  color: #92999f;
  font-size: 14px;
  font-weight: normal;
}

.footer p span{
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  display: block;

}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
  border-radius: 5px;
}

.search-button{
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

